import React from 'react';
import './remedies.css';
import Footer from '../../component/Footer/Footer';
import YouTube, { Options } from 'react-youtube';
import ReactDOM from "react-dom";
import { ReactSVG } from 'react-svg'


const ytVideo = [
  "JHhqLVZh1Gg",
  "V8fm5mjewag",
  "O38KFvZDd9A",
  "_I2AfPc57f4",
  "3Oz7F5lr8BY",
  "aAk7WL7u1Yw",
  "bXDTHqGYVkQ",
  "r6XjLWGGj4Q",
  "a4dhZ0lCL-o"
]

class Remedies extends React.Component<any, any> {

  constructor(props: any) {
    super(props);
    this.state = {
      selectedPartName: null,
      partID:null
    }
  }

  render() {
    const youtubeOpt: Options = {
      width: "800px",
      height: "450px",
      playerVars: {
        // https://developers.google.com/youtube/player_parameters
        autoplay: 0,
      },
    };
    return (
      <div className="remediesContainer">
        <div className="remediesHeaderContainer">
          <div className="remediesHeader">KAUNTING KAALAMAN!</div>
          <div className="remediesHeaderText">Kahit epektibo ang mga ito, limitado lamang ang tulong na mabibigay ng mga tutorial na ito. Mas maganda kapag ikaw ay magpunta sa mga propesyonal na physiotherapists para siguradong sigurado ang paggaling mo!</div>
        </div>
        <div className="remediesBodyContainer">
          <div className="remediesBodyHeader">Ano sumasakit sayo?
          </div>
          <div className="remediesBodyText">
            Gamit ng katawan sa baba, hanapin lamang at pindutin mo kung saan mo nararanasan ang sakit upang malaman ang mga dapat gawin para guminhawa ito. Tara at subukan na!
          </div>
          <div className="remediesContent">
            <div className="bodySelect">
              <ReactSVG
                renumerateIRIElements={false}
                beforeInjection={svg => {
                  svg.classList.add('svg-class-name')
                  svg.setAttribute('style', 'width: 18vw');
                  let svgClik = svg.querySelectorAll('g') as NodeListOf<SVGElement>
                  svgClik.forEach((part: SVGElement, index) => {
                    part.addEventListener("click", () => {
                      console.log("Click Part", part.id)
                      console.log("Click Index", index)
                      this.setState({ selectedPartName: part.id, partID: index })
                    })
                  })
                }}
                evalScripts="always"
                src={require("../../img/body2.svg")} />
            </div>
            <div className="youtubeContainer">
              <div className="remarksContainer">
                <div>Remedyo sa mga injury sa {this.state.selectedPartName}</div>
              </div>
              <YouTube containerClassName="youtube" videoId={ytVideo[this.state.partID]} opts={youtubeOpt} onReady={this._onReady} />
              <div className="remarksContainer">
                <div>Ito ba ay nakatulong?</div>
                <button className="yesBtn"> Oo!</button>
                <button className="noBtn"> Hindi!</button>
              </div>

            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }

  _onReady(event: any) {
    // access to player in all event handlers via event.target
    event.target.pauseVideo();
  }
}

export default Remedies;
